.Toogle-switch{
  margin: 10px 0px;;
  position: relative;
  /* display: inline-block; */
  width: 60px;
  height: 34px;
}

.Toogle-switch input{
  opacity: 0;
  width: 0;
  height: 0;
}


.slider{
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #ccc;
    -webkit-transition: .4s;
  transition: .4s;
}


.slider:before{
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
   -webkit-transition: .4s;
  transition: .4s;
}


input:checked + .slider{
  background-color: #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}


.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

